import React from 'react';
import {Button, Intent} from '@blueprintjs/core';
import PropTypes from 'prop-types';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {isFormValid} from "../../../../helpers";
import {addNetwork, loadNetworks} from '../../../../actions/NetworksActions';
import Toaster from "../../../Common/Toaster";
import {ADD_NETWORK} from "../../../../helpers/constants";
import _isEqual from "lodash/isEqual";

class New extends React.Component{

    state = {
        operator: '',
        type: '',
    };

    componentDidUpdate(prevProps){
        const {OPERATION_SUCCESSFUL: OLD_OPERATION_SUCCESSFUL, OPERATION_FAILED: OLD_OPERATION_FAILED} = prevProps;
        const {OPERATION_SUCCESSFUL, OPERATION_FAILED, history, authUser: {token}, loadNetworks} = this.props;

        const network = this.state.operator.toUpperCase();

        if(!_isEqual(OLD_OPERATION_SUCCESSFUL, OPERATION_SUCCESSFUL)){
            loadNetworks(token, {});

            if(OPERATION_SUCCESSFUL.action === ADD_NETWORK){
                history.goBack();
                Toaster.show({
                    message: `${network} Successfully Added ðŸ˜ƒ`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }
        }

        if(!_isEqual(OLD_OPERATION_FAILED, OPERATION_FAILED)){
            if(OPERATION_FAILED.action === ADD_NETWORK){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Add ${network} ðŸ˜ž`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
        }
    }

    _handleInputChange = (name, value) => {
        this.setState({[name]: value});
    };

    _handleAdd = () => {
        const {authUser: {token}, addNetwork} = this.props;
        const {operator, type} = this.state;

        let data = new FormData();

        data.append('operator', operator);
        data.append('type', type);

        addNetwork(token, data);
    };

    _isFormValid = () => {
        const {operator, type} = this.state;

        let required = {operator, type};

        return isFormValid(required);
    }

    render(){
        const {addingNetwork} = this.props;
        const {operator, type} = this.state;
        const disableSaveButton = !this._isFormValid();

        return [
            <div key="body" className="bp3-dialog-body">
                <label className="bp3-label">
                    Operator
                    <input
                        value={operator}
                        name={'operator'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
                <label className="bp3-label">
                    Network Type
                    <input
                        value={type}
                        name={'type'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
            </div>,
            <div key="footer" className="bp3-dialog-footer">
                <div className="bp3-dialog-footer-actions">
                    <Button
                        disabled={disableSaveButton}
                        loading={addingNetwork}
                        intent={Intent.PRIMARY}
                        onClick={this._handleAdd}
                        icon="tick"
                        text="save"
                    />
                </div>
            </div>
        ];
    }

    static propTypes = {
        history: PropTypes.object.isRequired,
        authUser: PropTypes.object.isRequired,
        OPERATION_SUCCESSFUL: PropTypes.object.isRequired,
        OPERATION_FAILED: PropTypes.object.isRequired,
        addNetwork: PropTypes.func.isRequired,
        loadNetworks: PropTypes.func.isRequired,
        addingNetwork: PropTypes.bool.isRequired,
        networks: PropTypes.array.isRequired,
    };
}

const mapStateToProps = (
    {OPERATION_SUCCESSFUL, OPERATION_FAILED, authUser, addingNetwork, networks}) => (
        {OPERATION_SUCCESSFUL, OPERATION_FAILED, authUser, addingNetwork, networks});

const mapDispatchToProps = dispatch => bindActionCreators({addNetwork, loadNetworks}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(New));


